.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 18rem;
    height: 21rem;
    margin: 4rem auto;
    background-color: #fee715ff;
    color: #949398FF;
    
  }
  
.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

.card img {
  max-width: 100%;
}

.container {
    padding: 2px 16px;
  }
  

.flexContainer {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      text-align: center;

  }